import React from 'react';
import { Link } from 'gatsby';
import { useEffect } from 'react';
import { Pushbar } from '../../vendor/pushbar.js/pushbar';

import './Menu.scss';
import '../../vendor/pushbar.js/pushbar.css';
import { Observer, GLOBAL_EVENT } from "../../Observer";
import { css } from 'glamor';

const menu_active = css({
    textDecoration: 'underline !important'
});

Observer.once(GLOBAL_EVENT.MENU_DOM_LOADED, () => {
    new Pushbar({
        blur: true,
        overlay: true,
    });
});
export const Menu = ({location}) => {
    useEffect(() => {
        Observer.emit(GLOBAL_EVENT.MENU_DOM_LOADED);
    });
    return (
        <React.Fragment>
            <nav className="menu d-none d-sm-flex">
                <div className="logo ml-4">
                    <Link to="/">
                        GAMMAFP
                    </Link>
                </div>
                <ul>
                    <li>
                        <Link to="/blog" className={ (location.pathname.startsWith('/blog')) ? menu_active : ''} >
                            Blog
                        </Link>
                    </li>
                    <li>
                        <Link to="/">
                            Games
                        </Link>
                    </li>
                    <li>
                        <Link to="/">
                            Menu
                        </Link>
                    </li>
                </ul>
            </nav>
            <div className="cellphone-menu-button d-sm-none">
                <span className="material-icons open" data-pushbar-target="cellphone-menu">menu</span>
            </div>
            <nav data-pushbar-id="cellphone-menu" data-pushbar-direction="left" className="cellphone-menu d-sm-none">
                <div className="logo-and-close">
                    <div className="logo">
                        <Link to="/" data-pushbar-close>
                            GAMMAFP
                        </Link>
                    </div>
                    <span data-pushbar-close className="material-icons close-button">close</span>
                </div>
                <div className="cellphone-menu--items">
                    <Link to="/" data-pushbar-close className="noselect">Blog</Link>
                    <Link to="/" data-pushbar-close className="noselect">Game</Link>
                    <Link to="/" data-pushbar-close className="noselect">Menu</Link>
                </div>
            </nav>
        </React.Fragment>
    )
}
