import React from 'react';
import './Footer.scss';
import { useStaticQuery, graphql, Link } from 'gatsby';

const Footer = () => {
    const data = useStaticQuery(graphql`
    query {
        site {
          siteMetadata {
            author
          }
        }
      }
    `);
    return (
        <footer className="mt-4">
            <div className="container">
                <div className="row d-flex align-self-stretch">
                    <div className="col-12 footer-text text-center">
                        © {new Date().getFullYear()}, {data.site.siteMetadata.author} | <Link to="/">Condiciones de usos</Link> |  <Link to="/">Privacidad</Link> | Contacta: gammafp@gmail.com
                    </div>
                </div>
            </div>
        </footer>
    )
}

export default Footer;